'use client';

import { useEffect, useState } from 'react';

declare global {
  interface Window {
    google: any;
    googleTranslateElementInit: () => void;
  }
}

export default function GoogleTranslate() {
  const [showPopup, setShowPopup] = useState(false);
  const [isLoaded, setIsLoaded] = useState(false);

  useEffect(() => {
    // Check if user has already made a language choice
    const hasChosenLanguage = localStorage.getItem('languageChoice');
    
    if (!hasChosenLanguage) {
      // Show popup after a short delay
      const timer = setTimeout(() => {
        setShowPopup(true);
      }, 1000);
      
      return () => clearTimeout(timer);
    }
  }, []);

  useEffect(() => {
    // Load Google Translate script
    const script = document.createElement('script');
    script.src = '//translate.google.com/translate_a/element.js?cb=googleTranslateElementInit';
    script.async = true;
    document.head.appendChild(script);

    // Initialize Google Translate
    window.googleTranslateElementInit = () => {
      new window.google.translate.TranslateElement({
        pageLanguage: 'en',
        includedLanguages: 'en,ar',
        layout: window.google.translate.TranslateElement.InlineLayout.SIMPLE,
        autoDisplay: false,
      }, 'google_translate_element');
      setIsLoaded(true);
    };

    return () => {
      if (document.head.contains(script)) {
        document.head.removeChild(script);
      }
    };
  }, []);

  const handleLanguageChoice = async (language: string) => {
    localStorage.setItem('languageChoice', language);
    setShowPopup(false);
    
    if (language === 'ar') {
      // Wait for Google Translate to be ready
      const waitForGoogleTranslate = () => {
        return new Promise<void>((resolve) => {
          const checkInterval = setInterval(() => {
            if (window.google && window.google.translate) {
              clearInterval(checkInterval);
              resolve();
            }
          }, 100);
          
          // Timeout after 5 seconds
          setTimeout(() => {
            clearInterval(checkInterval);
            resolve();
          }, 5000);
        });
      };

      await waitForGoogleTranslate();
      
      // Trigger Arabic translation
      if (window.google && window.google.translate) {
        const select = document.querySelector('.goog-te-combo') as HTMLSelectElement;
        if (select) {
          select.value = 'ar';
          select.dispatchEvent(new Event('change'));
        } else {
          // Fallback: try to trigger translation directly
          const iframe = document.querySelector('.goog-te-banner-frame') as HTMLIFrameElement;
          if (iframe && iframe.contentWindow) {
            iframe.contentWindow.postMessage({ type: 'translate', language: 'ar' }, '*');
          }
        }
      }
    }
    // If English, do nothing as it's the default
  };

  if (!showPopup) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg p-6 max-w-md w-full mx-4 shadow-xl">
        <div className="text-center">
          <h3 className="text-xl font-semibold text-gray-800 mb-4">
            Choose Your Language
          </h3>
          <p className="text-gray-600 mb-6">
            Select your preferred language for browsing our website
          </p>
          
          <div className="space-y-3">
            <button
              onClick={() => handleLanguageChoice('en')}
              className="w-full bg-blue-600 text-white py-3 px-4 rounded-lg hover:bg-blue-700 transition-colors font-medium"
            >
              Continue in English
            </button>
            
            <button
              onClick={() => handleLanguageChoice('ar')}
              className="w-full bg-green-600 text-white py-3 px-4 rounded-lg hover:bg-green-700 transition-colors font-medium"
            >
              ترجم إلى العربية
            </button>
          </div>
          
          <div className="mt-4">
            <div id="google_translate_element" className="hidden"></div>
          </div>
        </div>
      </div>
    </div>
  );
}
